
        
    .diagnosis_wrap_box {
      display: flex;
      flex-direction: row;             
      flex-wrap: nowrap;
      border: 1px solid black;
      width:100%;
      height:68vh;       
      margin: 0 auto;
  }

  .diagnosis_ad_box { 
      display:flex;
      flex: 1 0 20%;
      border: 1px solid black;
      margin: 0 auto;
      align-items:center;
      justify-content: center;
  }

  .diagnosis_ad_content{        
      border: 1px solid black;        
  }

  .diagnosis_index_box { 
      flex-direction: column;             
      flex-wrap: nowrap;
      display:flex;
      flex: 0 0 80%;
      border: 1px solid black;
      margin: 0 auto;
      align-items:center;
      justify-content: center;
  }    

  .diagnosis_choice_box {
      display: flex;        
      justify-content: center;
      align-items: center;        
      text-align: center;                
      border: 1px solid black;
      /* width: 90%; */
      /* height:20%; */
  }

  .diagnosis_choice_result_box {
      display: flex;
      flex-direction: row;
      flex-wrap: nowrap;                
      text-align: center;                   
      justify-content: center;
      border: 1px solid black;   
  }


  .diagnosis_answer_box {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      margin: 0 auto;
      text-align: center;
      border: 1px solid black;
      width:35vh;
  }
  
  .diagnosis_image_box {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;        
      text-align: center;
      border: 1px solid black;
  }        