
@media screen and (max-width: 999px) {

  .diagnosis_result_section_wrap {
    border: 31px solid green;
    padding:3px;
  }
  
  .diagnosis_result_analystic_summary_box_detail {
    flex-direction: column;
  }
  
  .diagnosis_result_detect_wrap {
    flex-direction: column;
  }

  .diagnosis_result_detect_item {
    display: flex;
    flex-direction:row;
    /* margin: 0 auto; */
    /* align-items: center; */
  }
  .diagnosis_result_detect_item_content {
    display: flex;
    flex-direction:row;
    margin: 0 auto;
    align-items: center;
  }
  .diagnosis_result_predict_disease_item_summary {
    flex-direction: column;
  }
  .diagnosis_result_predict_disease_list_box {
    flex-direction: column;
  }
  
}


